@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@900&display=swap);
html {
  font-size: 10px;
  font-family: 'Noto Sans JP', sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #eee;
  color: #333;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}
  
.title {
  font-size: 4.4rem;
  font-family: 'Noto Sans JP', sans-serif;
  text-align: center;
  text-transform: uppercase;
  color: rgb(229, 9, 20);
}
.form {
  display: grid;
}
  
.input {
  font-size: 1.6rem;
  font-family: inherit;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
  width: 100%;
}

.button {
  font-size: 1.5rem;
  font-weight: bold;
  background-color: rgba(229, 9, 20, 0.95);
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgba(229, 9, 20, 0.75);
}

.message {
  font-size: 2rem;
  text-align: center;
  color: #333;
  margin-top: 4rem;
}

@media (min-width:786px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    -webkit-align-items: center;
            align-items: center;
  }

  .input {
    margin-bottom: 0;
  }
}

.card-list {
  margin-top: 4rem;
}

.card {
  padding: 4rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  background-color: white;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.card--image {
  display: block;
  margin: 0 auto;
  border-radius: 10px;
}

@media (min-width:786px) {
  .card {
    display: grid;
    grid-template-columns: 185px auto;
    grid-gap: 4rem;
  }
  
  .card--title {
    margin-top: 0;
  }
}

